body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  background-color:#EFF3F7;
  height: 100%;
}

html, #root{
  height:100%;
}
.App {
  padding: 50px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  font-size: 14px;
}

.App--loading{
  display: block;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  height: 100%;
}

.movie{
    background-color:white;
    width:40%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items:flex-start;
            align-items:flex-start;
    -webkit-flex-wrap:wrap;
            flex-wrap:wrap;
    margin-bottom:50px;
    text-overflow: ellipsis;
    padding:0 20px;
    box-shadow: 0 8px 38px rgba(133, 133, 133, 0.3), 0 5px 12px rgba(133, 133, 133,0.22);
}

.movieColumn{
    width:30%;
    box-sizing:border-box;
    text-overflow: ellipsis;
}

.movieColumn:last-child{
    padding:20px 0;
    width:60%;
}

.movie h1{
    font-size:20px;
    font-weight: 600;
}

.movieGenres .movieGenre{
    margin-right:10px;
    color:#B4B5BD;
}

.movie .moviePoster{
    max-width: 100%;
    position: relative;
    top:-20px;
    box-shadow: -10px 19px 38px rgba(83, 83, 83, 0.3), 10px 15px 12px rgba(80,80,80,0.22);
}

.movie .movieSynopsis {
    text-overflow: ellipsis;
    color:#B4B5BD;
    overflow: hidden;
}

@media screen and (min-width:320px) and (max-width:667px){
    .Movie{
        width:100%;
    }
}

@media screen and (min-width:320px) and (max-width:667px) and (orientation: portrait){
    .movie{
        width:100%;
        -webkit-flex-direction: column;
                flex-direction: column;
    }
    .moviePoster{
        top:0;
        left:0;
        width:100%;
    }
    .movieColumn{
        width:100%!important;
    }
}
